export default {
	name: 'home',
	data() {
		return {
			globalData: {},
			PageCur: 'current',
			list: {},
			list1: {},
			list2: {}
		};
	},
	onLoad() {
		this.globalData = getApp().globalData;
		this.loadData();
	},
	methods: {
		loadData() {
			this.$minApi
				.goods({ category_id: 2, sell: 1 })
				.then(res => {
					this.list1 = res;
				})
				.catch(err => {
					console.log(err);
				});
			this.$minApi
				.goods({ category_id: 2, sell: 0 })
				.then(res => {
					this.list2 = res;
				})
				.catch(err => {
					console.log(err);
				});
		},
		NavChange: function(e) {
			this.PageCur = e.currentTarget.dataset.cur;
			this.initList();
		},
		initList() {
			if (this.PageCur == 'current') {
				this.list = this.list1;
			} else if (this.PageCur == 'future') {
				this.list = this.list2;
			}
		},
		toGoodsDetails(id) {
			uni.navigateTo({
				url: '/pages/goods/details?id=' + id + '&keyword=' + this.globalData.userInfo.keyword
			});
		},
		toPay(id) {
			uni.navigateTo({
				url: '/pages/order/ready?goods=' + id
			});
		}
	}
};
